@import url('https://fonts.googleapis.com/css?family=Roboto:400,500&display=swap');

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
  background-color: #333;
  
  .app {
    font-family: Roboto, sans-serif;

    .gallery {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      height: fit-content;
      padding: 16px;

      .photoContainer {
        position: relative;
        display: flex;
        width: fit-content;
        height: fit-content;
        margin-bottom: 16px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 25);

        .closeButton {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          width: 30px;
          height: 30px;
          position: absolute;
          top: 0;
          right: 0;
          background-color: red;
          border-radius: 50%;
          margin: 5px;

          span {
            display: block;
            background-color: #fff;
            width: 20px;
            height: 2px;

            &:nth-child(1) {
              transform: rotate(45deg);
              position: absolute;
            }

            &:nth-child(2) {
              transform: rotate(-45deg);
              position: absolute;
            }
          }
        }

        img {
          max-width: 100%;
          height: auto;
        }
      }
    }

    .errorScreen {
      display: flex;
      justify-content: center;
      align-items: center;
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: #212121;
      z-index: 2;
      display: none;

      span {
        color: #fff;
        font-size: 20px;
        line-height: 30px;
        max-width: 80%;
        text-align: center;
      }
    }
  }
}
