@import url(https://fonts.googleapis.com/css?family=Roboto:400,500&display=swap);
*{margin:0;padding:0;outline:0;box-sizing:border-box}html,body,#root{height:100%;background-color:#333}html .app,body .app,#root .app{font-family:Roboto, sans-serif}html .app .gallery,body .app .gallery,#root .app .gallery{display:flex;flex-direction:column;align-items:center;width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:16px}html .app .gallery .photoContainer,body .app .gallery .photoContainer,#root .app .gallery .photoContainer{position:relative;display:flex;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin-bottom:16px;box-shadow:0 0 10px #000}html .app .gallery .photoContainer .closeButton,body .app .gallery .photoContainer .closeButton,#root .app .gallery .photoContainer .closeButton{display:flex;flex-direction:column;justify-content:center;align-items:center;width:30px;height:30px;position:absolute;top:0;right:0;background-color:red;border-radius:50%;margin:5px}html .app .gallery .photoContainer .closeButton span,body .app .gallery .photoContainer .closeButton span,#root .app .gallery .photoContainer .closeButton span{display:block;background-color:#fff;width:20px;height:2px}html .app .gallery .photoContainer .closeButton span:nth-child(1),body .app .gallery .photoContainer .closeButton span:nth-child(1),#root .app .gallery .photoContainer .closeButton span:nth-child(1){-webkit-transform:rotate(45deg);transform:rotate(45deg);position:absolute}html .app .gallery .photoContainer .closeButton span:nth-child(2),body .app .gallery .photoContainer .closeButton span:nth-child(2),#root .app .gallery .photoContainer .closeButton span:nth-child(2){-webkit-transform:rotate(-45deg);transform:rotate(-45deg);position:absolute}html .app .gallery .photoContainer img,body .app .gallery .photoContainer img,#root .app .gallery .photoContainer img{max-width:100%;height:auto}html .app .errorScreen,body .app .errorScreen,#root .app .errorScreen{display:flex;justify-content:center;align-items:center;position:fixed;top:0;right:0;bottom:0;left:0;background-color:#212121;z-index:2;display:none}html .app .errorScreen span,body .app .errorScreen span,#root .app .errorScreen span{color:#fff;font-size:20px;line-height:30px;max-width:80%;text-align:center}

